.chart-container {
  width: 100%;
}

.chart-header {
  margin-bottom: 1em;
  color: $color-primary;
  text-align: center;
}

.data-header {
  h2,
  h3 {
    margin-bottom: 0;
  }

  h2 {
    font-size: 1em;
  }

  h3 {
    font-size: 2em;
  }

  .block-label {
    display: inline;
  }
}

.row-header .vcard {
  margin: 0.5em -0.5em 0;

  > * {
    display: block;
    padding: 0 0.5em;
    font-size: 14px;

    @include breakpoint(medium) {
      font-size: 16px;
    }

    @include breakpoint(large) {
      display: inline-block;
    }

    &:last-child {
      border-right: 0;
    }
  }

  .email,
  .tel {
    &:before {
      padding-right: 5px;
      font-family: 'FontAwesome';
      font-style: normal;
    }
  }

  .email {
    &:before {
      content: '\f199';
    }
  }

  .tel {
    &:before {
      content: '\f098';
    }
  }

  .title {
    padding-right: 0;
    color: $color-gold;
    border-right: 0;
  }

  .vcard-unavailable {
    font-style: italic;
  }
}

.row-subheader {
  h2 {
    max-width: 20em;
    margin-bottom: 1em;
    font-size: 1.5em;

    @include breakpoint(medium) {
      font-size: 2em;
    }
  }

  .button-ask {
    border: 0;

    .button-info-icon {
      color: $color-gray-dark;
      background-color: $white;
    }

    .button-info-title {
      color: $white;
      background-color: $color-gray;

      @include breakpoint(small only) {
        font-size: 18px;
      }
    }
  }
}

.row-subheader-agency {
  font-size: 0.8em;
  text-align: center;

  @include breakpoint(medium) {
    font-size: 0.9em;
    text-align: left;
  }

  @include breakpoint(large) {
    font-size: 1em;
  }

  h2 {
    margin: 0;
    font-size: 1.5em;
  }

  h3 {
    margin: 0;
    font-size: 2em;
  }

  .department-logo {
    height: auto;
    max-width: 150px;

    @include breakpoint(medium) {
      max-width: 100%;
    }
  }
}

.row .stats-block {
  margin: -1rem;

  a {
    display: block;
    padding: $global-padding;
    color: $color-base;
    background-color: $white;
    border: $global-border-width solid $global-border-color;

    &.active,
    &:active,
    &:focus,
    &:hover {
      background-color: $color-gold;

      dt {
        color: $color-base;
      }
    }
  }

  dt {
    margin-bottom: 0.5em;
    font-size: 1.25em;
    font-weight: $global-weight-normal;
    color: $color-primary-alt-darkest;
  }

  dd {
    &.more-info {
      margin-top: 1em;
      font-size: 0.9em;
      line-height: 1.5;
    }

    .stat {
      font-family: $alt-font-family;
      font-size: 3.5em;
      font-weight: $global-alt-weight-bold;
    }
  }
}
